
:host {
  * {
    box-sizing: border-box;
    font-family: 'Prelo';
  }

  .pButton {
    line-height: normal;
    border: 0.0625rem solid transparent;
    cursor: pointer;
    border-radius: 4px;
    position: relative;

    &.full {
      width: 100%;
      min-width: 0px;
    }

    /*BUTTON STATES*/
    &:disabled {
      cursor: auto;
      pointer-events: none;
      box-shadow: none;
    }

    &:active {
      transform: translateY(0.0625rem);
    }

    &:focus {
      outline: none;
    }

    .renderText {
      color: currentColor;

      p-font {
        color: currentColor;
      }
    }

    .icon {
      margin-left: 4px;
    }

    /*BUTTON COLORS*/
    &.primary {
      background: #091944;
      border: 1px solid #091944;
      color: #ffffff;

      &:hover,
      &:focus {
        background: #091944;
      }

      &:disabled {
        background: #edeff2;
        color: #c1c7d4;
      }
    }

    &.secondary {
      background: #FFDD00;
      border: 1px solid #FFDD00;
      color: #0F265C;

      &:hover {
        background: #FFDD00;
        color: #0F265C;
        border: none;
      }

      &:focus {
        background: #FFDD00;
        color: #0F265C;
      }

      &:disabled {
        background: #edeff2;
        color: #c1c7d4;
        border: none;
      }
    }

    &.tertiary {
      background: #DEE3ED;
      border: 1px solid #DEE3ED;
      color: #0F265C;

      &:hover {
        background: #DEE3ED;
        color: #0F265C;
        border: none;
      }

      &:focus {
        background: #DEE3ED;
        color: #0F265C;
      }

      &:disabled {
        background: #edeff2;
        color: #c1c7d4;
        border: none;
      }
    }

    &.action {
      background: #D5E4F2;
      border: 1px solid #D5E4F2;
      color: #2F7ABF;

      &:hover {
        background: #D5E4F2;
        color: #2F7ABF;
        border: none;
      }

      &:focus {
        background: #D5E4F2;
        color: #2F7ABF;
      }

      &:disabled {
        background: #edeff2;
        color: #c1c7d4;
        border: none;
      }
    }

    &.dark {
      background: #565675;
      color: #FFFFFF;

      &:hover {
        background: #6F7189;
        color: #FFFFFF;
      }

      &:focus {
        background: #6F7189;
        color: #FFFFFF;
      }

      &:disabled {
        background: #edeff2;
        color: #c1c7d4;
      }
    }

    &.none {
      background: #EEF1F6;
      color: #212942;

      &:hover {
        background: #F8F9FE;
        color: #212942;
      }

      &:focus {
        background: #F8F9FE;
        color: #212942;
      }

      &:disabled {
        background: #edeff2;
        color: #c1c7d4;
      }
    }

    &.green {
      background: #5CD393;
      color: #FFFFFF;

      &:hover {
        background: #6FE1A1;
        color: #FFFFFF;
      }

      &:focus {
        background: #6FE1A1;
        color: #FFFFFF;
      }

      &:disabled {
        background: #edeff2;
        color: #c1c7d4;
      }
    }

    &.danger {
      background: #ec5073;
      color: #ffff;

      &:hover {
        background: #ee798e;
        color: #ffff;
      }

      &:focus {
        background: #ee798e;
        color: #ffff;
      }

      &:disabled {
        background: #EDEFF2;
        color: #C1C7D4;
      }
    }

    &.blue1 {
      background: #3C96F7;
      color: #ffff;

      &:hover {
        background: #5DAAF8;
        color: #ffff;
      }

      &:focus {
        background: #5DAAF8;
        color: #ffff;
      }

      &:disabled {
        background: #EDEFF2;
        color: #C1C7D4;
      }
    }

    /*BUTTON SIZES*/
    &.normal {
      padding: 0 26px;
      height: 48px;
      min-width: 157px;
    }

    &.sm {
      padding: 0 12px;
      height: 32px;
      min-width: 115px;
    }

    &.field-md {
      padding: 0 15px;
      height: 36px;
      min-width: 120px;
    }

  }

}
