

:host {
    * {
        box-sizing: border-box;
        font-family: 'Prelo';
        font-size: 14px;
    }
    .PInput {
      position: relative;
      .input {
        // overflow: visible;
        // display: block;
        width: 100%;
        padding: 10px 14px;
        height: 48px;
        color: #6F7D9D;
        border: 1px solid #929292;
        border-radius: 4px;
        outline:none;
        &:disabled, input:disabled:hover {
          background-color: #EAE9EF;
          border: 1px solid #EAE9EF;
          font-weight: 500;
          color: #919CB1;
          cursor: auto;
        }
        &::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
          color: #6F7D9D;
          font-size: 14px;
          font-weight: 400;
        }
      }

      .icon {
        position: absolute;
        right: 16px;
        top: 12px;
      }

      .lock {
        background-color: #EDEFF2;
        border: none;
        color: #212942;
        pointer-events: none;
      }
    }

    .error-text:not(:disabled) {
        &.error {
          border: 1px solid #ff3e38;
        }
    }
    .lowercase {
        text-transform: none !important;
    }
    .input:-ms-input-placeholder, .input::-ms-input-placeholder {
      color: #212942;
      vertical-align: middle;
    }

    .input::-ms-input-placeholder {
        color: #212942;
    }
}
