import { Component, Prop, EventEmitter, Event, State, Element, Listen, h } from '@stencil/core';
import { Modelable } from '../../../globals/interfaces/modelable.interface';
import { Type, Size, TypeErrors, Color } from '../../../globals/types/types';

@Component({
  tag: 'p-input',
  styleUrl: 'p-input.scss',
  shadow: true
})
export class PInput implements Modelable {

  @Element() el: HTMLElement;
  @Prop() placeholder: string = '';
  @Prop() type: Type = 'text';
  @Prop() size: Size = 'md';
  @Prop() name: string = 'text';
  @Prop({mutable: true}) value: string;
  @Prop({mutable: true}) disable: boolean = false;
  @Prop({mutable: true}) lock: boolean = false;
  @Prop({mutable: true}) error: boolean = false;
  @Prop({mutable: true}) colorerror: TypeErrors = 'error';
  @Prop({mutable: true}) maxlength: string;
  @Prop({mutable: true}) minlength: string;
  @Prop({mutable: true}) lowercase: boolean = false;
  @Prop({mutable: true}) focusable: boolean = false;
  @Prop({mutable: true}) iseventemmiter: boolean = true;
  @Prop() iconname: string = '';
  @Prop() iconcolor: Color = 'none';
  @Prop() iconbgcolor: Color = 'none';
  @Prop() iconviewbox: string = '';

  @Event() inputChange: EventEmitter;
  @Event() changeValue: EventEmitter;
  @Event() elementBlur: EventEmitter;
  @Event() elementKeydown: EventEmitter;
  @Event() elementFocus: EventEmitter;
  @Event() elementKeyEnter: EventEmitter;

  @State() touched: boolean = false;
  @State() position: number;


  elementHasBlur(ev: FocusEvent) {

    this.position = undefined;

    if (this.value && this.value.length > 0) {

      this.value = this.value;
    }

    this.elementBlur.emit(ev);

    ev.preventDefault();
    ev.stopPropagation();
  }

  componentDidUpdate() {

    if(this.position !== undefined) {

      this.el.shadowRoot.querySelector('input').setSelectionRange(this.position, this.position);
    }

    if(this.value && this.value.length > 0) {

      this.value = this.value.replace(/^\s+/,"");
      this.changeValue.emit(this.value);
    }
  }

  componentDidLoad() {

    if (this.focusable) {

      setTimeout( () => {

        this.el.shadowRoot.querySelector('input').focus();
      }, 0);
    }
  }

  render() {
      return (
        <div class={this.getMainClassList()}>
                  {
          this.iconname &&
          <p-svg class="icon" name={this.iconname} viewbox={this.iconviewbox} width="24"
            height="24"
            color={this.disable ? 'lightgray' : 'white'}>
          </p-svg>
        }
          <input
            maxlength={this.maxlength}
            minlength={this.minlength}
            type={this.type }
            name={this.name}
            id={this.name}
            placeholder={this.placeholder}
            onChange={ev => this.change(ev)}
            class={this.getInputClassList()}
            value={this.value}
            onBlur={ ev => this.elementHasBlur(ev) }
            onFocus={(ev) => this.elementFocus.emit(ev)}
            onKeyDown={(ev) => this.elementKeydown.emit(ev)}
            disabled={this.disable}
            readonly={this.lock}
            >
          </input>
        </div>
      );
  }

  getMainClassList() {
    return {
      ['PInput']: true,
      [`disable`]: this.disable
    }
  }

  getInputClassList() {
    return {
      [`input`]: true,
      [`error-text ${this.colorerror}`]: this.error,
      [`lowercase`]: this.lowercase,
      [`lock`]: this.lock,
      [`${this.size}`]: true
    }
  }

  @Listen('keydown')
  handleKeyDown(ev: KeyboardEvent) {
    if (ev.keyCode == 90 && ev.ctrlKey) {
      ev.preventDefault();
      ev.stopPropagation();
    }

    if (ev.key == "Enter") {
      this.elementKeyEnter.emit(this.value);
    }
  }

  change(_event) {
    this.inputChange.emit(this.value);
  }

}
