import { Component, Event, EventEmitter, h, Prop } from '@stencil/core';
import { ButtonType, Color, FontType, Size, ThemeColor } from '../../../globals/types/types';


@Component({
  tag: 'p-button',
  styleUrl: 'p-button.scss',
  shadow: true
})
export class PButton {
  @Event() buttonClicked: EventEmitter;

  @Prop() text: string = 'Default';
  @Prop() color: ThemeColor = 'primary';
  @Prop() size: Size = 'normal';
  @Prop() type: ButtonType = 'default';
  @Prop() disable: boolean = false;


  // ICON - PROPS
  @Prop() iconname: string = '';
  @Prop() iconcolor: Color = 'none';
  @Prop() iconbgcolor: Color = 'none';
  @Prop() iconviewbox: string = '';

  render() {
    return (
      <button class={this.mainClass()} onClick={ev => this.click(ev)} disabled={this.disable}>
        {
          (this.type !== 'icon') &&
          this.renderText()
        }
      </button>
    );

  }

  renderText() {
    return (
      <div class="renderText">
        <p-font type={this.getButtonFont()} text={this.text} color='current'></p-font>
        {
          this.iconname &&
          <p-svg class="icon" name={this.iconname} viewbox={this.iconviewbox} width="15"
            height="15"
            color={this.disable ? 'lightgray' : 'white'}>
          </p-svg>
        }
      </div>
    )
  }

  mainClass() {

   return {
      [`pButton`]: true,
      [`${this.color}`]: true,
      [`${this.size}`]: true,
      [`${this.type}`]: true,
    };
  }

  private getButtonFont(): FontType {
    switch (this.size) {
      case 'field-md': return 'button_sm';
      case 'normal': return 'button_md';
      case 'sm': return 'button_sm';
    }

    return `button_${this.size}` as FontType;
  }

  click(_event: MouseEvent) {
    if (!this.disable) {
      this.buttonClicked.emit();
    }
  }
}
