import { Component, Prop, h} from "@stencil/core";
import { Color, Measures } from "../../../globals/types/types";
import { propsIcon } from "./p-svg.interface"
import { IconName } from "./iicon";
import { getIcon } from "./icon-list";

@Component({
    tag: 'p-svg',
    styleUrl: 'p-svg.scss',
    shadow: true
  })
export class PSvg {

    @Prop() public name: IconName | string = IconName.DEFAULT;
    @Prop() public viewbox: string;
    @Prop() public width: string = '24';
    @Prop() public height: string = '24';
    @Prop() public color: Color = 'lightgray';
    @Prop() public fill: string = 'none';
    @Prop() public pointer: boolean = true;
    @Prop() public measure: Measures = 'px';
    @Prop() public preserveAspectRatio: string = 'xMinYMin';

    public propsIcon: propsIcon;

    /* istanbul ignore next */
    render() {
      return (
        <i  tabindex="-1"
            style={this.getDimensions()}
            class={this.getClassList()}
            innerHTML={this.getSvg(this.name)}>
        </i>
      );
    }
    getClassList() {
      return {
        [`${this.color}`]: true,
        [`pointer`]: this.pointer,
        [`PIconSvg`]: true
      }
    }
    getDimensions() {
      return {
        width: `${this.width}${this.measure}`,
        height: `${this.height}${this.measure}`
      }
    }

    getSvg(name: IconName | string) {
      const icon = getIcon(name);
      return `
        <svg focusable="false" width=${this.width} height=${this.height} fill=${this.fill}
          viewBox='${this.viewbox? this.viewbox: icon.viewbox}'
          preserveAspectRatio=${this.preserveAspectRatio}
          xmlns="http://www.w3.org/2000/svg">
            ${icon.path}
        </svg>
      `;
    }
}


